import { Command } from 'commander';
import fs from 'fs/promises';
import fsSync from 'fs';
import path from 'path';
import { globby } from 'globby';
import { execSync } from 'child_process';
import { logger, LogLevel } from '../utils/logger.js';
import { AIRouter, AIRequest } from '../services/ai-router.js';
import { TaskType, Message } from '../interfaces/ai-provider.js';

interface ReviewOptions {
  output?: string;
  format?: 'text' | 'json' | 'markdown' | 'github';
  severity?: 'all' | 'error' | 'warning' | 'info';
  language?: string;
  framework?: string;
  provider?: string;
  model?: string;
  local?: boolean;
  context?: number;
  diff?: boolean;
  suggestions?: boolean;
  verbose?: boolean;
}

interface ReviewIssue {
  file: string;
  line?: number;
  column?: number;
  severity: 'error' | 'warning' | 'info';
  category: string;
  message: string;
  suggestion?: string;
  codeSnippet?: string;
}

interface ReviewResult {
  summary: {
    totalFiles: number;
    totalIssues: number;
    errorCount: number;
    warningCount: number;
    infoCount: number;
    overallRating: string;
  };
  issues: ReviewIssue[];
  recommendations: string[];
  metrics?: {
    codeQuality: number;
    maintainability: number;
    security: number;
    performance: number;
  };
}

export default function reviewCommand(program: Command) {
  program
    .command('review')
    .description('AI-powered code review with intelligent analysis')
    .argument('[files...]', 'Files or patterns to review (defaults to common code files)')
    .option('-o, --output <file>', 'Output review results to file')
    .option('-f, --format <format>', 'Output format: text, json, markdown, or github', 'text')
    .option('-s, --severity <level>', 'Minimum severity level: all, error, warning, info', 'all')
    .option('-l, --language <lang>', 'Programming language (auto-detected if not specified)')
    .option('--framework <framework>', 'Framework context (react, express, django, etc.)')
    .option('--provider <name>', 'Specific AI provider to use')
    .option('--model <model>', 'Specific model to use')
    .option('--local', 'Prefer local models', false)
    .option('-c, --context <lines>', 'Context lines around issues', '3')
    .option('--diff', 'Review only changed files (git diff)', false)
    .option('--suggestions', 'Include fix suggestions', true)
    .option('-v, --verbose', 'Show detailed output', false)
    .action(async (files: string[], options: ReviewOptions) => {
      // Set log level
      if (options.verbose) {
        logger.setLevel(LogLevel.DEBUG);
      }

      logger.task('Code Review', 'start', 'Starting AI-powered code review');

      try {
        // Resolve files to review
        const filesToReview = await resolveFilesToReview(files, options);
        
        if (filesToReview.length === 0) {
          logger.warn('No files found to review');
          return;
        }

        logger.debug(`Found ${filesToReview.length} files to review:`, filesToReview.map(f => path.relative(process.cwd(), f)));

        // Initialize AI Router
        const router = await initializeAIRouter();

        // Review files
        const reviewResults: ReviewResult = {
          summary: {
            totalFiles: filesToReview.length,
            totalIssues: 0,
            errorCount: 0,
            warningCount: 0,
            infoCount: 0,
            overallRating: 'Unknown'
          },
          issues: [],
          recommendations: []
        };

        for (let i = 0; i < filesToReview.length; i++) {
          const filePath = filesToReview[i];
          if (!filePath) continue;

          logger.task('File Review', 'progress', `Reviewing ${path.relative(process.cwd(), filePath)} (${i + 1}/${filesToReview.length})`);

          try {
            const fileReview = await reviewFile(filePath, router, options);
            
            // Merge results
            reviewResults.issues.push(...fileReview.issues);
            reviewResults.recommendations.push(...fileReview.recommendations);

          } catch (error) {
            logger.error(`Failed to review ${filePath}:`, error);
            reviewResults.issues.push({
              file: filePath,
              severity: 'error',
              category: 'Review Error',
              message: `Failed to review file: ${error instanceof Error ? error.message : 'Unknown error'}`
            });
          }
        }

        // Calculate summary statistics
        calculateSummaryStats(reviewResults);

        // Generate overall recommendations
        if (reviewResults.issues.length > 0) {
          const overallRecommendations = await generateOverallRecommendations(reviewResults, router, options);
          reviewResults.recommendations.unshift(...overallRecommendations);
        }

        logger.task('Code Review', 'complete', `Review completed: ${reviewResults.summary.totalIssues} issues found`);

        // Handle output
        if (options.output) {
          await handleReviewOutput(options.output, reviewResults, options);
        } else {
          displayReviewResults(reviewResults, options);
        }

      } catch (error) {
        logger.task('Code Review', 'error', error instanceof Error ? error.message : 'Unknown error');
        logger.error('Code review failed:', error);
        process.exit(1);
      }
    });
}

/**
 * Resolve files to review based on input and options
 */
async function resolveFilesToReview(files: string[], options: ReviewOptions): Promise<string[]> {
  let filesToReview: string[] = [];

  if (options.diff) {
    // Get changed files from git diff
    filesToReview = await getChangedFiles();
  } else if (files.length > 0) {
    // Use provided files/patterns
    filesToReview = await globby(files, { 
      gitignore: true,
      onlyFiles: true,
      absolute: true
    });
  } else {
    // Default patterns for common code files
    const defaultPatterns = [
      '**/*.{js,jsx,ts,tsx}',
      '**/*.{py,java,go,rs,php,rb}',
      '**/*.{c,cpp,h,hpp,cs}',
      '!node_modules/**',
      '!dist/**',
      '!build/**',
      '!coverage/**',
      '!.git/**'
    ];
    
    filesToReview = await globby(defaultPatterns, {
      gitignore: true,
      onlyFiles: true,
      absolute: true,
      cwd: process.cwd()
    });
  }

  // Filter by language if specified
  if (options.language) {
    filesToReview = filesToReview.filter(file => {
      const ext = path.extname(file).toLowerCase();
      return getLanguageForExtension(ext) === options.language!.toLowerCase();
    });
  }

  return filesToReview;
}

/**
 * Get changed files from git diff
 */
async function getChangedFiles(): Promise<string[]> {
  try {
    const output = execSync('git diff --name-only HEAD', { encoding: 'utf8' });
    return output.trim().split('\n')
      .filter((file: string) => file.trim() !== '')
      .map((file: string) => path.resolve(file));
  } catch {
    logger.warn('Could not get git diff, falling back to all files');
    return [];
  }
}

/**
 * Review a single file
 */
async function reviewFile(
  filePath: string,
  router: AIRouter,
  options: ReviewOptions
): Promise<{ issues: ReviewIssue[], recommendations: string[] }> {
  
  const fileContent = await fs.readFile(filePath, 'utf8');
  const language = options.language || getLanguageForExtension(path.extname(filePath));
  const relativePath = path.relative(process.cwd(), filePath);

  // Build review prompt
  const systemPrompt = buildReviewSystemPrompt(language, options);
  const userPrompt = buildFileReviewPrompt(relativePath, fileContent, language, options);

  const messages: Message[] = [
    { role: 'system', content: systemPrompt },
    { role: 'user', content: userPrompt }
  ];

  const aiRequest: AIRequest = {
    messages,
    taskType: TaskType.CODE_REVIEW,
    preferLocal: options.local,
    preferredProvider: options.provider,
    context: {
      language,
      framework: options.framework,
      projectType: detectProjectType(process.cwd())
    },
    options: {
      temperature: 0.2, // Low temperature for consistent analysis
      maxTokens: 3000,
      responseFormat: 'json'
    }
  };

  const response = await router.route(aiRequest);
  return parseReviewResponse(response.content, filePath, options);
}

/**
 * Build system prompt for code review
 */
function buildReviewSystemPrompt(language: string, options: ReviewOptions): string {
  const parts = [
    `You are an expert ${language.toUpperCase()} code reviewer with deep knowledge of best practices, security, and performance optimization.`,
    '',
    'Your task is to perform a thorough code review focusing on:',
    '- Code quality and style consistency',
    '- Potential bugs and logic errors',
    '- Security vulnerabilities',
    '- Performance issues',
    '- Maintainability and readability',
    '- Best practices adherence',
    '',
    'Severity levels:',
    '- error: Critical issues that must be fixed',
    '- warning: Important issues that should be addressed',
    '- info: Suggestions for improvement',
    ''
  ];

  if (options.framework) {
    parts.push(`Consider ${options.framework} framework-specific best practices.`);
  }

  if (options.suggestions) {
    parts.push('Provide specific fix suggestions for each issue when possible.');
  }

  parts.push(
    'Respond with valid JSON in this format:',
    '{',
    '  "issues": [',
    '    {',
    '      "line": number,',
    '      "severity": "error|warning|info",',
    '      "category": "string",',
    '      "message": "string",',
    '      "suggestion": "string (optional)"',
    '    }',
    '  ],',
    '  "recommendations": ["string"],',
    '  "metrics": {',
    '    "codeQuality": 0-10,',
    '    "maintainability": 0-10,',
    '    "security": 0-10,',
    '    "performance": 0-10',
    '  }',
    '}'
  );

  return parts.join('\n');
}

/**
 * Build file-specific review prompt
 */
function buildFileReviewPrompt(
  filePath: string,
  content: string,
  language: string,
  options: ReviewOptions
): string {
  const lines = content.split('\n');
  const numberedContent = lines
    .map((line, index) => `${(index + 1).toString().padStart(4, ' ')}: ${line}`)
    .join('\n');

  return [
    `Please review this ${language} file: ${filePath}`,
    '',
    'File content with line numbers:',
    '```',
    numberedContent,
    '```',
    '',
    `Focus on finding issues with severity level "${options.severity}" and above.`,
    'Provide line numbers for all issues found.'
  ].join('\n');
}

/**
 * Parse AI review response
 */
function parseReviewResponse(
  response: string,
  filePath: string,
  options: ReviewOptions
): { issues: ReviewIssue[], recommendations: string[] } {
  try {
    const parsed = JSON.parse(response);
    
    const issues: ReviewIssue[] = (parsed.issues || []).map((issue: unknown) => {
      const issueObj = issue as Record<string, unknown>;
      return {
        file: filePath,
        line: issueObj.line as number,
        severity: (issueObj.severity as string) || 'info',
        category: (issueObj.category as string) || 'General',
        message: (issueObj.message as string) || 'No message provided',
        suggestion: issueObj.suggestion as string
      };
    });

    // Filter by severity
    const filteredIssues = filterBySeverity(issues, options.severity);

    return {
      issues: filteredIssues,
      recommendations: parsed.recommendations || []
    };

  } catch {
    logger.debug('Failed to parse JSON response, extracting text issues');
    // Fallback to text parsing
    return parseTextResponse(response, filePath);
  }
}

/**
 * Filter issues by severity level
 */
function filterBySeverity(issues: ReviewIssue[], severityFilter?: string): ReviewIssue[] {
  if (!severityFilter || severityFilter === 'all') {
    return issues;
  }

  const severityOrder: Record<string, number> = {
    'error': 3,
    'warning': 2,
    'info': 1
  };

  const minLevel = severityOrder[severityFilter] || 1;
  
  return issues.filter(issue => {
    const issueLevel = severityOrder[issue.severity] || 1;
    return issueLevel >= minLevel;
  });
}

/**
 * Fallback text response parser
 */
function parseTextResponse(response: string, filePath: string): { issues: ReviewIssue[], recommendations: string[] } {
  const lines = response.split('\n');
  const issues: ReviewIssue[] = [];
  const recommendations: string[] = [];

  for (const line of lines) {
    // Try to extract line numbers and issues
    const lineMatch = line.match(/line\s+(\d+).*?(error|warning|info)/i);
    if (lineMatch) {
      issues.push({
        file: filePath,
        line: parseInt(lineMatch[1]!),
        severity: (lineMatch[2]?.toLowerCase() as 'error' | 'warning' | 'info') || 'info',
        category: 'General',
        message: line.trim()
      });
    }
  }

  return { issues, recommendations };
}

/**
 * Calculate summary statistics
 */
function calculateSummaryStats(reviewResults: ReviewResult): void {
  const issues = reviewResults.issues;
  reviewResults.summary.totalIssues = issues.length;
  reviewResults.summary.errorCount = issues.filter(i => i.severity === 'error').length;
  reviewResults.summary.warningCount = issues.filter(i => i.severity === 'warning').length;
  reviewResults.summary.infoCount = issues.filter(i => i.severity === 'info').length;

  // Calculate overall rating
  if (reviewResults.summary.errorCount > 0) {
    reviewResults.summary.overallRating = 'Needs Improvement';
  } else if (reviewResults.summary.warningCount > reviewResults.summary.totalFiles * 2) {
    reviewResults.summary.overallRating = 'Fair';
  } else if (reviewResults.summary.warningCount > 0) {
    reviewResults.summary.overallRating = 'Good';
  } else {
    reviewResults.summary.overallRating = 'Excellent';
  }
}

/**
 * Generate overall recommendations
 */
async function generateOverallRecommendations(
  reviewResults: ReviewResult,
  router: AIRouter,
  options: ReviewOptions
): Promise<string[]> {
  try {
    const summaryPrompt = `Based on this code review summary, provide 3-5 high-level recommendations for improving the codebase:

Issues found: ${reviewResults.summary.totalIssues}
- Errors: ${reviewResults.summary.errorCount}
- Warnings: ${reviewResults.summary.warningCount}
- Info: ${reviewResults.summary.infoCount}

Files reviewed: ${reviewResults.summary.totalFiles}

Most common issues:
${getMostCommonIssues(reviewResults.issues).join('\n')}

Provide specific, actionable recommendations.`;

    const aiRequest: AIRequest = {
      messages: [
        { role: 'system', content: 'You are a senior software architect providing code review guidance.' },
        { role: 'user', content: summaryPrompt }
      ],
      taskType: TaskType.CODE_REVIEW,
      preferLocal: options.local,
      options: {
        temperature: 0.3,
        maxTokens: 500
      }
    };

    const response = await router.route(aiRequest);
    return response.content.split('\n').filter(line => line.trim().length > 0);

  } catch (error) {
    logger.debug('Failed to generate overall recommendations:', error);
    return ['Review completed with issues found. Consider addressing high-severity issues first.'];
  }
}

/**
 * Get most common issue categories
 */
function getMostCommonIssues(issues: ReviewIssue[]): string[] {
  const categoryCount = new Map<string, number>();
  
  issues.forEach(issue => {
    const count = categoryCount.get(issue.category) || 0;
    categoryCount.set(issue.category, count + 1);
  });

  return Array.from(categoryCount.entries())
    .sort((a, b) => b[1] - a[1])
    .slice(0, 5)
    .map(([category, count]) => `- ${category}: ${count} issues`);
}

