/**
 * Handle file output for review results
 */
async function handleReviewOutput(
  outputPath: string,
  reviewResults: ReviewResult,
  options: ReviewOptions
): Promise<void> {
  let content: string;
  let finalPath = outputPath;

  if (options.format === 'json') {
    content = JSON.stringify(reviewResults, null, 2);
    if (!path.extname(finalPath)) finalPath += '.json';
  } else if (options.format === 'markdown') {
    content = generateMarkdownReport(reviewResults);
    if (!path.extname(finalPath)) finalPath += '.md';
  } else if (options.format === 'github') {
    content = generateGitHubReport(reviewResults);
    if (!path.extname(finalPath)) finalPath += '.md';
  } else {
    content = generateTextReport(reviewResults);
    if (!path.extname(finalPath)) finalPath += '.txt';
  }

  const dir = path.dirname(finalPath);
  await fs.mkdir(dir, { recursive: true });
  await fs.writeFile(finalPath, content, 'utf8');
  logger.success(`Review results saved to ${finalPath}`);
}

/**
 * Generate markdown report
 */
function generateMarkdownReport(reviewResults: ReviewResult): string {
  const lines = [
    '# Code Review Report',
    '',
    '## Summary',
    `- **Files reviewed:** ${reviewResults.summary.totalFiles}`,
    `- **Total issues:** ${reviewResults.summary.totalIssues}`,
    `- **Errors:** ${reviewResults.summary.errorCount}`,
    `- **Warnings:** ${reviewResults.summary.warningCount}`, 
    `- **Info:** ${reviewResults.summary.infoCount}`,
    `- **Overall rating:** ${reviewResults.summary.overallRating}`,
    ''
  ];

  if (reviewResults.issues.length > 0) {
    lines.push('## Issues');
    reviewResults.issues.forEach((issue, index) => {
      const relativePath = path.relative(process.cwd(), issue.file);
      lines.push(`### ${index + 1}. ${issue.severity.toUpperCase()} in \`${relativePath}\`${issue.line ? `:${issue.line}` : ''}`);
      lines.push(`**Category:** ${issue.category}`);
      lines.push(`**Message:** ${issue.message}`);
      if (issue.suggestion) {
        lines.push(`**Suggestion:** ${issue.suggestion}`);
      }
      lines.push('');
    });
  }

  if (reviewResults.recommendations.length > 0) {
    lines.push('## Recommendations');
    reviewResults.recommendations.forEach((rec, index) => {
      lines.push(`${index + 1}. ${rec}`);
    });
    lines.push('');
  }

  return lines.join('\n');
}

/**
 * Generate GitHub-formatted report
 */
function generateGitHubReport(reviewResults: ReviewResult): string {
  // Similar to markdown but with GitHub-specific formatting
  return generateMarkdownReport(reviewResults);
}

/**
 * Generate plain text report
 */
function generateTextReport(reviewResults: ReviewResult): string {
  const lines = [
    'CODE REVIEW REPORT',
    '==================',
    '',
    'SUMMARY',
    '-------',
    `Files reviewed: ${reviewResults.summary.totalFiles}`,
    `Total issues: ${reviewResults.summary.totalIssues}`,
    `Errors: ${reviewResults.summary.errorCount}`,
    `Warnings: ${reviewResults.summary.warningCount}`,
    `Info: ${reviewResults.summary.infoCount}`,
    `Overall rating: ${reviewResults.summary.overallRating}`,
    ''
  ];

  if (reviewResults.issues.length > 0) {
    lines.push('ISSUES', '------');
    reviewResults.issues.forEach((issue, index) => {
      const relativePath = path.relative(process.cwd(), issue.file);
      lines.push(`${index + 1}. ${issue.severity.toUpperCase()} in ${relativePath}${issue.line ? `:${issue.line}` : ''}`);
      lines.push(`   Category: ${issue.category}`);
      lines.push(`   Message: ${issue.message}`);
      if (issue.suggestion) {
        lines.push(`   Suggestion: ${issue.suggestion}`);
      }
      lines.push('');
    });
  }

  if (reviewResults.recommendations.length > 0) {
    lines.push('RECOMMENDATIONS', '---------------');
    reviewResults.recommendations.forEach((rec, index) => {
      lines.push(`${index + 1}. ${rec}`);
    });
  }

  return lines.join('\n');
}

/**
 * Detect project type from current directory
 */
function detectProjectType(cwd: string): string {
  try {
    if (fsSync.existsSync(path.join(cwd, 'package.json'))) return 'nodejs';
    if (fsSync.existsSync(path.join(cwd, 'requirements.txt'))) return 'python';
    if (fsSync.existsSync(path.join(cwd, 'Cargo.toml'))) return 'rust';
    if (fsSync.existsSync(path.join(cwd, 'go.mod'))) return 'go';
    if (fsSync.existsSync(path.join(cwd, 'pom.xml'))) return 'java';
    return 'general';
  } catch {
    return 'general';
  }
}

/**
 * Get programming language from file extension
 */
function getLanguageForExtension(ext: string): string {
  const languageMap: Record<string, string> = {
    '.js': 'javascript',
    '.jsx': 'javascript',
    '.ts': 'typescript',
    '.tsx': 'typescript',
    '.py': 'python',
    '.java': 'java',
    '.go': 'go',
    '.rs': 'rust',
    '.php': 'php',
    '.rb': 'ruby',
    '.c': 'c',
    '.cpp': 'cpp',
    '.cc': 'cpp',
    '.cxx': 'cpp',
    '.h': 'c',
    '.hpp': 'cpp',
    '.cs': 'csharp',
    '.swift': 'swift',
    '.kt': 'kotlin',
    '.scala': 'scala',
    '.clj': 'clojure',
    '.ex': 'elixir',
    '.exs': 'elixir'
  };

  return languageMap[ext.toLowerCase()] || 'text';
}

/**
 * Initialize AI Router - placeholder implementation
 */
async function initializeAIRouter(): Promise<AIRouter> {
  const providers = new Map();
  
  const config = {
    providers,
    fallbackEnabled: true,
    autoSelectModel: true,
    costOptimization: false,
    privacyFirst: false
  };

  return new AIRouter(config);
}