import { Command } from 'commander';
import fs from 'fs/promises';
import path from 'path';
import { logger, LogLevel } from '../utils/logger.js';
import { AIRouter, AIRequest } from '../services/ai-router.js';
import { TaskType, Message } from '../interfaces/ai-provider.js';

interface CodeOptions {
  output?: string;
  language?: string;
  framework?: string;
  style?: 'concise' | 'verbose' | 'documented';
  tests?: boolean;
  comments?: boolean;
  provider?: string;
  model?: string;
  local?: boolean;
  overwrite?: boolean;
  verbose?: boolean;
}

export default function codeCommand(program: Command) {
  program
    .command('code')
    .description('AI-powered code generation with intelligent model selection')
    .argument('<prompt>', 'Code generation prompt describing what you want to build')
    .option('-o, --output <file>', 'Output file path (optional, will print to console if not specified)')
    .option('-l, --language <lang>', 'Target programming language (auto-detected if not specified)')
    .option('-f, --framework <framework>', 'Framework or library to use (e.g., react, express, fastapi)')
    .option('-s, --style <style>', 'Code style: concise, verbose, or documented', 'documented')
    .option('--tests', 'Include unit tests', false)
    .option('--comments', 'Include detailed comments', true)
    .option('--provider <name>', 'Specific AI provider to use (openai, anthropic, google, lmstudio, etc.)')
    .option('--model <model>', 'Specific model to use')
    .option('--local', 'Prefer local models', false)
    .option('--overwrite', 'Overwrite existing file without confirmation', false)
    .option('-v, --verbose', 'Show detailed output', false)
    .action(async (prompt: string, options: CodeOptions) => {
      // Set log level
      if (options.verbose) {
        logger.setLevel(LogLevel.DEBUG);
      }

      logger.task('Code Generation', 'start', `Generating code: "${prompt}"`);

      try {
        // Validate prompt
        if (!prompt || prompt.trim().length === 0) {
          logger.error('Code generation prompt cannot be empty');
          process.exit(1);
        }

        // Initialize AI Router (this would normally be done elsewhere)
        const router = await initializeAIRouter();

        // Detect programming language if not specified
        const language = options.language || detectLanguage(prompt, options.framework);
        logger.debug(`Detected/specified language: ${language}`);

        // Prepare context information
        const context = {
          language,
          framework: options.framework,
          projectType: detectProjectType(process.cwd())
        };

        // Build system prompt for code generation
        const systemPrompt = buildCodeGenerationPrompt(options, language, context);
        
        // Prepare messages
        const messages: Message[] = [
          { role: 'system', content: systemPrompt },
          { role: 'user', content: prompt }
        ];

        // Prepare AI request
        const aiRequest: AIRequest = {
          messages,
          taskType: TaskType.CODE_GENERATION,
          preferLocal: options.local,
          preferredProvider: options.provider,
          context,
          options: {
            temperature: 0.3, // Lower temperature for more deterministic code
            maxTokens: options.style === 'concise' ? 2000 : 4000,
            responseFormat: 'text'
          }
        };

        logger.task('AI Processing', 'progress', 'Routing to optimal provider...');

        // Generate code using AI Router
        const response = await router.route(aiRequest);

        logger.task('AI Processing', 'complete', `Used ${response.provider} (${response.model})`);

        // Extract and clean the generated code
        const generatedCode = extractCodeFromResponse(response.content, language);

        // Display usage information
        if (response.usage) {
          logger.debug(`Tokens: ${response.usage.totalTokens} (${response.usage.promptTokens} + ${response.usage.completionTokens})`);
          if (response.usage.cost) {
            logger.debug(`Estimated cost: $${response.usage.cost.toFixed(4)}`);
          }
        }

        // Handle output
        if (options.output) {
          await handleFileOutput(options.output, generatedCode, options.overwrite ?? false, language);
        } else {
          // Print to console with syntax highlighting
          console.log('\n' + '‚ïê'.repeat(60));
          console.log(`Generated ${language.toUpperCase()} Code:`);
          console.log('‚ïê'.repeat(60));
          console.log(generatedCode);
          console.log('‚ïê'.repeat(60) + '\n');
        }

        logger.task('Code Generation', 'complete', 
          options.output 
            ? `Code saved to ${options.output}` 
            : 'Code generated successfully'
        );

      } catch (error) {
        logger.task('Code Generation', 'error', error instanceof Error ? error.message : 'Unknown error');
        logger.error('Code generation failed:', error);
        process.exit(1);
      }
    });
}

/**
 * Initialize AI Router with available providers
 */
async function initializeAIRouter(): Promise<AIRouter> {
  // This would normally be imported from a service factory
  // For now, we'll create a minimal implementation
  const providers = new Map();
  
  // In a real implementation, this would initialize actual providers
  // providers.set('openai', new OpenAIProvider());
  // providers.set('anthropic', new AnthropicProvider());
  // etc.
  
  const config = {
    providers,
    fallbackEnabled: true,
    autoSelectModel: true,
    costOptimization: false,
    privacyFirst: false
  };

  return new AIRouter(config);
}

/**
 * Detect programming language from prompt and framework
 */
function detectLanguage(prompt: string, framework?: string): string {
  const lowerPrompt = prompt.toLowerCase();
  
  // Framework-based detection
  if (framework) {
    const frameworkLanguages: Record<string, string> = {
      'react': 'typescript',
      'vue': 'typescript', 
      'angular': 'typescript',
      'express': 'javascript',
      'fastapi': 'python',
      'django': 'python',
      'flask': 'python',
      'spring': 'java',
      'rails': 'ruby',
      'laravel': 'php',
      'gin': 'go',
      'actix': 'rust'
    };
    
    if (frameworkLanguages[framework.toLowerCase()]) {
      return frameworkLanguages[framework.toLowerCase()]!;
    }
  }
  
  // Keyword-based detection
  if (lowerPrompt.includes('python') || lowerPrompt.includes('django') || lowerPrompt.includes('fastapi')) {
    return 'python';
  }
  if (lowerPrompt.includes('javascript') || lowerPrompt.includes('node') || lowerPrompt.includes('js')) {
    return 'javascript';
  }
  if (lowerPrompt.includes('typescript') || lowerPrompt.includes('ts') || lowerPrompt.includes('react')) {
    return 'typescript';
  }
  if (lowerPrompt.includes('java') && !lowerPrompt.includes('javascript')) {
    return 'java';
  }
  if (lowerPrompt.includes('go') || lowerPrompt.includes('golang')) {
    return 'go';
  }
  if (lowerPrompt.includes('rust')) {
    return 'rust';
  }
  if (lowerPrompt.includes('php')) {
    return 'php';
  }
  if (lowerPrompt.includes('ruby')) {
    return 'ruby';
  }
  if (lowerPrompt.includes('c++') || lowerPrompt.includes('cpp')) {
    return 'cpp';
  }
  if (lowerPrompt.includes('c#') || lowerPrompt.includes('csharp')) {
    return 'csharp';
  }
  
  // Default to TypeScript as it's popular for modern development
  return 'typescript';
}

/**
 * Detect project type from current directory
 */
function detectProjectType(cwd: string): string {
  try {
    // Check for package.json (Node.js/JavaScript/TypeScript project)
    if (require('fs').existsSync(path.join(cwd, 'package.json'))) {
      return 'nodejs';
    }
    
    // Check for requirements.txt or pyproject.toml (Python project)
    if (require('fs').existsSync(path.join(cwd, 'requirements.txt')) || 
        require('fs').existsSync(path.join(cwd, 'pyproject.toml'))) {
      return 'python';
    }
    
    // Check for Cargo.toml (Rust project)
    if (require('fs').existsSync(path.join(cwd, 'Cargo.toml'))) {
      return 'rust';
    }
    
    // Check for go.mod (Go project)
    if (require('fs').existsSync(path.join(cwd, 'go.mod'))) {
      return 'go';
    }
    
    // Check for pom.xml or build.gradle (Java project)
    if (require('fs').existsSync(path.join(cwd, 'pom.xml')) || 
        require('fs').existsSync(path.join(cwd, 'build.gradle'))) {
      return 'java';
    }
    
    return 'general';
  } catch {
    return 'general';
  }
}

/**
 * Build system prompt for code generation
 */
function buildCodeGenerationPrompt(
  options: CodeOptions, 
  language: string, 
  context: { language: string; framework?: string; projectType: string }
): string {
  const parts = [
    `You are an expert ${language.toUpperCase()} developer. Generate high-quality, production-ready code.`,
    '',
    'Requirements:',
    `- Language: ${language}`,
  ];

  if (context.framework) {
    parts.push(`- Framework: ${context.framework}`);
  }

  if (context.projectType !== 'general') {
    parts.push(`- Project type: ${context.projectType}`);
  }

  parts.push(`- Code style: ${options.style}`);

  if (options.comments) {
    parts.push('- Include detailed comments and documentation');
  }

  if (options.tests) {
    parts.push('- Include comprehensive unit tests');
  }

  parts.push(
    '',
    'Best practices to follow:',
    '- Follow language-specific conventions and idioms',
    '- Use meaningful variable and function names',
    '- Handle errors appropriately',
    '- Optimize for readability and maintainability',
    '- Include type hints/annotations where applicable',
    '- Follow security best practices',
    '',
    'Format your response with the code wrapped in appropriate code blocks with language specification.',
    'If multiple files are needed, clearly separate them with file names as comments.'
  );

  return parts.join('\n');
}

/**
 * Extract code from AI response
 */
function extractCodeFromResponse(response: string, language: string): string {
  // Try to extract code from markdown code blocks
  const codeBlockRegex = new RegExp(`\`\`\`${language}\\n([\\s\\S]*?)\`\`\``, 'gi');
  const generalCodeBlockRegex = /```[\w]*\n([\s\S]*?)```/gi;
  
  let match = codeBlockRegex.exec(response);
  if (match && match[1]) {
    return match[1].trim();
  }
  
  // Try general code blocks
  match = generalCodeBlockRegex.exec(response);
  if (match && match[1]) {
    return match[1].trim();
  }
  
  // If no code blocks found, return the whole response cleaned up
  return response.trim();
}

/**
 * Handle file output with confirmation
 */
async function handleFileOutput(
  outputPath: string, 
  code: string, 
  overwrite: boolean, 
  language: string
): Promise<void> {
  try {
    // Add file extension if missing
    const ext = path.extname(outputPath);
    if (!ext) {
      const extensions: Record<string, string> = {
        'javascript': '.js',
        'typescript': '.ts',
        'python': '.py',
        'java': '.java',
        'go': '.go',
        'rust': '.rs',
        'php': '.php',
        'ruby': '.rb',
        'cpp': '.cpp',
        'csharp': '.cs'
      };
      
      const defaultExt = extensions[language] || '.txt';
      outputPath += defaultExt;
    }

    // Check if file exists
    const fileExists = await fs.access(outputPath).then(() => true).catch(() => false);
    
    if (fileExists && !overwrite) {
      logger.warn(`File ${outputPath} already exists. Use --overwrite to replace it.`);
      return;
    }

    // Create directory if it doesn't exist
    const dir = path.dirname(outputPath);
    await fs.mkdir(dir, { recursive: true });

    // Write file
    await fs.writeFile(outputPath, code, 'utf8');
    logger.success(`Code saved to ${outputPath}`);
    
  } catch (error) {
    throw new Error(`Failed to write file: ${error instanceof Error ? error.message : 'Unknown error'}`);
  }
}