import { Command } from 'commander';
import { execSync, exec } from 'child_process';
import { promisify } from 'util';
import fs from 'fs/promises';
import path from 'path';
import { logger, LogLevel } from '../utils/logger.js';
import { AIRouter, AIRequest } from '../services/ai-router.js';
import { TaskType, Message } from '../interfaces/ai-provider.js';

const execAsync = promisify(exec);

interface CommitOptions {
  message?: string;
  type?: 'conventional' | 'semantic' | 'standard' | 'auto';
  scope?: string;
  breaking?: boolean;
  provider?: string;
  model?: string;
  local?: boolean;
  push?: boolean;
  amend?: boolean;
  dry?: boolean;
  interactive?: boolean;
  template?: string;
  verbose?: boolean;
}

interface GitChange {
  type: 'added' | 'modified' | 'deleted' | 'renamed';
  file: string;
  oldFile?: string;
  stats?: {
    additions: number;
    deletions: number;
  };
}

interface CommitAnalysis {
  type: string;
  scope?: string;
  description: string;
  body?: string;
  breaking?: boolean;
  issues?: string[];
  coauthors?: string[];
}

export default function commitCommand(program: Command) {
  program
    .command('commit')
    .description('AI-powered commit message generation with intelligent analysis')
    .option('-m, --message <msg>', 'Custom message prefix or template')
    .option('-t, --type <type>', 'Commit message format: conventional, semantic, standard, or auto', 'auto')
    .option('-s, --scope <scope>', 'Commit scope (for conventional commits)')
    .option('--breaking', 'Mark as breaking change', false)
    .option('--provider <name>', 'Specific AI provider to use')
    .option('--model <model>', 'Specific model to use')
    .option('--local', 'Prefer local models', false)
    .option('-p, --push', 'Push after committing', false)
    .option('--amend', 'Amend last commit', false)
    .option('--dry', 'Generate message without committing', false)
    .option('-i, --interactive', 'Interactive mode for message refinement', false)
    .option('--template <file>', 'Use custom commit message template')
    .option('-v, --verbose', 'Show detailed output', false)
    .action(async (options: CommitOptions) => {
      // Set log level
      if (options.verbose) {
        logger.setLevel(LogLevel.DEBUG);
      }

      logger.task('Commit Generation', 'start', 'Analyzing changes for commit message');

      try {
        // Check if we're in a git repository
        await checkGitRepository();

        // Get git status and changes
        const changes = await analyzeGitChanges(options);
        
        if (changes.length === 0 && !options.amend) {
          logger.warn('No changes staged for commit. Stage your changes first with "git add".');
          return;
        }

        logger.debug(`Found ${changes.length} changes to commit`);

        // Initialize AI Router
        const router = await initializeAIRouter();

        // Generate commit message
        const commitAnalysis = await generateCommitMessage(changes, router, options);

        // Format final commit message
        const finalMessage = formatCommitMessage(commitAnalysis, options);

        logger.task('Commit Generation', 'complete', 'Generated commit message');

        // Display the generated message
        displayCommitMessage(finalMessage, changes);

        if (options.dry) {
          logger.info('Dry run mode - no commit created');
          return;
        }

        // Interactive refinement if requested
        let finalCommitMessage = finalMessage;
        if (options.interactive) {
          finalCommitMessage = await interactiveRefinement(finalMessage);
        }

        // Create the commit
        await createCommit(finalCommitMessage, options);

        // Push if requested
        if (options.push) {
          await pushCommit();
        }

        logger.success('Commit created successfully');

      } catch (error) {
        logger.task('Commit Generation', 'error', error instanceof Error ? error.message : 'Unknown error');
        logger.error('Commit generation failed:', error);
        process.exit(1);
      }
    });
}

/**
 * Check if current directory is a git repository
 */
async function checkGitRepository(): Promise<void> {
  try {
    execSync('git rev-parse --git-dir', { stdio: 'ignore' });
  } catch {
    throw new Error('Not a git repository. Initialize with "git init" first.');
  }
}

/**
 * Analyze git changes for commit message generation
 */
async function analyzeGitChanges(options: CommitOptions): Promise<GitChange[]> {
  const changes: GitChange[] = [];

  try {
    let gitCommand = 'git diff --cached --name-status';
    if (options.amend) {
      gitCommand = 'git diff HEAD~1 --name-status';
    }

    const { stdout } = await execAsync(gitCommand);
    const lines = stdout.trim().split('\n').filter(line => line.trim() !== '');

    for (const line of lines) {
      const [status, ...fileParts] = line.split('\t');
      if (!status) continue;

      const file = fileParts.join('\t');
      if (!file) continue;

      let changeType: GitChange['type'];
      let oldFile: string | undefined;

      switch (status[0]) {
        case 'A':
          changeType = 'added';
          break;
        case 'M':
          changeType = 'modified';
          break;
        case 'D':
          changeType = 'deleted';
          break;
        case 'R':
          changeType = 'renamed';
          const [oldPath] = file.split('\t');
          oldFile = oldPath;
          break;
        default:
          changeType = 'modified';
      }

      // Get file stats for better analysis
      const stats = await getFileStats(file, options.amend);

      changes.push({
        type: changeType,
        file: changeType === 'renamed' ? file.split('\t')[1] || file : file,
        oldFile,
        stats
      });
    }

    return changes;

  } catch (error) {
    logger.debug('Error analyzing git changes:', error);
    return [];
  }
}

/**
 * Get file change statistics
 */
async function getFileStats(file: string, isAmend = false): Promise<{ additions: number; deletions: number; } | undefined> {
  try {
    const command = isAmend 
      ? `git diff HEAD~1 --numstat -- "${file}"`
      : `git diff --cached --numstat -- "${file}"`;
    
    const { stdout } = await execAsync(command);
    const [additions, deletions] = stdout.trim().split('\t');
    
    return {
      additions: parseInt(additions || '0') || 0,
      deletions: parseInt(deletions || '0') || 0
    };
  } catch {
    return undefined;
  }
}

/**
 * Generate commit message using AI
 */
async function generateCommitMessage(
  changes: GitChange[],
  router: AIRouter,
  options: CommitOptions
): Promise<CommitAnalysis> {
  
  // Get diff content for better context
  const diffContent = await getDiffContent(options.amend);
  
  // Build commit analysis prompt
  const systemPrompt = buildCommitSystemPrompt(options);
  const userPrompt = buildCommitUserPrompt(changes, diffContent, options);

  const messages: Message[] = [
    { role: 'system', content: systemPrompt },
    { role: 'user', content: userPrompt }
  ];

  const aiRequest: AIRequest = {
    messages,
    taskType: TaskType.SUMMARIZATION,
    preferLocal: options.local,
    preferredProvider: options.provider,
    options: {
      temperature: 0.3, // Balanced for creativity and consistency
      maxTokens: 1000,
      responseFormat: 'json'
    }
  };

  const response = await router.route(aiRequest);
  return parseCommitAnalysis(response.content, options);
}

/**
 * Get diff content for analysis
 */
async function getDiffContent(isAmend = false): Promise<string> {
  try {
    const command = isAmend ? 'git diff HEAD~1' : 'git diff --cached';
    const { stdout } = await execAsync(command);
    
    // Limit diff size to avoid overwhelming the AI
    const maxDiffSize = 8000;
    if (stdout.length > maxDiffSize) {
      return stdout.substring(0, maxDiffSize) + '\n... (diff truncated)';
    }
    
    return stdout;
  } catch {
    return '';
  }
}

/**
 * Build system prompt for commit message generation
 */
function buildCommitSystemPrompt(options: CommitOptions): string {
  const parts = [
    'You are an expert software developer creating commit messages.',
    'Analyze the provided git changes and generate a clear, concise commit message.',
    ''
  ];

  switch (options.type) {
    case 'conventional':
      parts.push(
        'Use Conventional Commits format:',
        '<type>[optional scope]: <description>',
        '',
        'Types: feat, fix, docs, style, refactor, test, chore, perf, ci, build, revert',
        'Keep description under 50 characters, use imperative mood',
        'Add body for context if needed (wrap at 72 characters)',
        'Add "BREAKING CHANGE:" footer if applicable'
      );
      break;
    case 'semantic':
      parts.push(
        'Use semantic commit format with emoji prefixes:',
        '‚ú® feat: new features',
        'üêõ fix: bug fixes', 
        'üìö docs: documentation',
        'üíÑ style: formatting, missing semi colons',
        '‚ôªÔ∏è refactor: refactoring code',
        '‚úÖ test: adding tests',
        'üîß chore: maintenance tasks'
      );
      break;
    case 'standard':
      parts.push(
        'Use clear, descriptive commit messages:',
        '- Start with a verb in imperative mood',
        '- Keep first line under 50 characters',
        '- Add detailed body if needed',
        '- Reference issues/tickets if applicable'
      );
      break;
    case 'auto':
      parts.push(
        'Choose the most appropriate format based on the project context.',
        'Default to conventional commits unless project suggests otherwise.',
        'Analyze the change patterns to determine the best format.'
      );
      break;
  }

  parts.push(
    '',
    'Respond with JSON in this format:',
    '{',
    '  "type": "commit_type_or_emoji",',
    '  "scope": "optional_scope",',
    '  "description": "commit_description",',
    '  "body": "optional_detailed_explanation",',
    '  "breaking": false,',
    '  "issues": ["#123"],',
    '  "coauthors": ["Name <email>"]',
    '}'
  );

  return parts.join('\n');
}

/**
 * Build user prompt with change analysis
 */
function buildCommitUserPrompt(
  changes: GitChange[],
  diffContent: string,
  options: CommitOptions
): string {
  const parts = [];

  // Add custom message context if provided
  if (options.message) {
    parts.push(`User provided message context: "${options.message}"`);
  }

  if (options.scope) {
    parts.push(`Preferred scope: ${options.scope}`);
  }

  parts.push('', 'Files changed:');
  
  changes.forEach(change => {
    const stats = change.stats ? ` (+${change.stats.additions}/-${change.stats.deletions})` : '';
    parts.push(`- ${change.type.toUpperCase()}: ${change.file}${stats}`);
    if (change.oldFile) {
      parts.push(`  Renamed from: ${change.oldFile}`);
    }
  });

  if (diffContent.trim()) {
    parts.push('', 'Diff summary (first 200 lines):');
    const lines = diffContent.split('\n');
    const truncatedDiff = lines.slice(0, 200).join('\n');
    parts.push('```diff');
    parts.push(truncatedDiff);
    parts.push('```');
  }

  parts.push('', 'Generate an appropriate commit message for these changes.');

  return parts.join('\n');
}

/**
 * Parse AI commit analysis response
 */
function parseCommitAnalysis(response: string, options: CommitOptions): CommitAnalysis {
  try {
    const parsed = JSON.parse(response);
    return {
      type: parsed.type || 'feat',
      scope: parsed.scope || options.scope,
      description: parsed.description || 'Update files',
      body: parsed.body,
      breaking: parsed.breaking || options.breaking,
      issues: parsed.issues || [],
      coauthors: parsed.coauthors || []
    };
  } catch {
    // Fallback parsing from text
    return parseTextCommitMessage(response, options);
  }
}

/**
 * Fallback text parsing for commit analysis
 */
function parseTextCommitMessage(response: string, options: CommitOptions): CommitAnalysis {
  const lines = response.split('\n').filter(line => line.trim());
  const firstLine = lines[0] || 'Update files';
  
  // Try to extract type from first line
  const conventionalMatch = firstLine.match(/^(\w+)(?:\(([^)]+)\))?: (.+)$/);
  if (conventionalMatch) {
    return {
      type: conventionalMatch[1] || 'feat',
      scope: conventionalMatch[2] || options.scope,
      description: conventionalMatch[3] || 'Update files',
      body: lines.slice(1).join('\n') || undefined,
      breaking: options.breaking
    };
  }

  return {
    type: 'feat',
    scope: options.scope,
    description: firstLine,
    body: lines.slice(1).join('\n') || undefined,
    breaking: options.breaking
  };
}

/**
 * Format final commit message based on type
 */
function formatCommitMessage(analysis: CommitAnalysis, options: CommitOptions): string {
  const parts = [];

  switch (options.type) {
    case 'conventional':
      let header = analysis.type;
      if (analysis.scope) {
        header += `(${analysis.scope})`;
      }
      header += `: ${analysis.description}`;
      parts.push(header);
      break;

    case 'semantic':
      const emoji = getEmojiForType(analysis.type);
      parts.push(`${emoji} ${analysis.type}: ${analysis.description}`);
      break;

    case 'standard':
      parts.push(analysis.description);
      break;

    case 'auto':
      // Auto-detect format based on project or use conventional as default
      let autoHeader = analysis.type;
      if (analysis.scope) {
        autoHeader += `(${analysis.scope})`;
      }
      autoHeader += `: ${analysis.description}`;
      parts.push(autoHeader);
      break;
  }

  // Add body if present
  if (analysis.body) {
    parts.push('', analysis.body);
  }

  // Add breaking change notice
  if (analysis.breaking) {
    parts.push('', 'BREAKING CHANGE: This commit introduces breaking changes');
  }

  // Add issue references
  if (analysis.issues && analysis.issues.length > 0) {
    parts.push('', `Closes ${analysis.issues.join(', ')}`);
  }

  // Add co-authors
  if (analysis.coauthors && analysis.coauthors.length > 0) {
    parts.push('');
    analysis.coauthors.forEach(author => {
      parts.push(`Co-authored-by: ${author}`);
    });
  }

  return parts.join('\n');
}

/**
 * Get emoji for commit type
 */
function getEmojiForType(type: string): string {
  const emojiMap: Record<string, string> = {
    'feat': '‚ú®',
    'fix': 'üêõ',
    'docs': 'üìö',
    'style': 'üíÑ',
    'refactor': '‚ôªÔ∏è',
    'test': '‚úÖ',
    'chore': 'üîß',
    'perf': '‚ö°',
    'ci': 'üë∑',
    'build': 'üì¶',
    'revert': '‚è™'
  };

  return emojiMap[type] || '‚ú®';
}

