/**
 * Interactive refinement of commit message
 */
async function interactiveRefinement(
  message: string
): Promise<string> {
  console.log('Enter "r" to regenerate, "e" to edit, or press Enter to use current message:');
  
  const readline = require('readline');
  const rl = readline.createInterface({
    input: process.stdin,
    output: process.stdout
  });

  return new Promise((resolve) => {
    rl.question('> ', async (answer: string) => {
      rl.close();
      
      if (answer.toLowerCase() === 'r') {
        logger.info('Regenerating commit message...');
        // Would regenerate with slightly different parameters
        resolve(message); // Simplified for now
      } else if (answer.toLowerCase() === 'e') {
        logger.info('Edit mode not implemented yet. Using current message.');
        resolve(message);
      } else {
        resolve(message);
      }
    });
  });
}

/**
 * Create the actual commit
 */
async function createCommit(message: string, options: CommitOptions): Promise<void> {
  try {
    let command = 'git commit';
    
    if (options.amend) {
      command += ' --amend';
    }
    
    // Use a temporary file for multi-line messages
    const tempFile = path.join(process.cwd(), '.git', 'COMMIT_EDITMSG_TEMP');
    await fs.writeFile(tempFile, message, 'utf8');
    
    command += ` -F "${tempFile}"`;
    
    logger.debug(`Executing: ${command}`);
    
    execSync(command, { stdio: 'inherit' });
    
    // Clean up temp file
    try {
      await fs.unlink(tempFile);
    } catch {
      // Ignore cleanup errors
    }
    
  } catch (error) {
    throw new Error(`Failed to create commit: ${error instanceof Error ? error.message : 'Unknown error'}`);
  }
}

/**
 * Push commit to remote
 */
async function pushCommit(): Promise<void> {
  try {
    logger.task('Git Push', 'start', 'Pushing to remote repository');
    
    execSync('git push', { stdio: 'inherit' });
    
    logger.task('Git Push', 'complete', 'Successfully pushed to remote');
    
  } catch (error) {
    logger.task('Git Push', 'error', 'Failed to push to remote');
    throw new Error(`Failed to push: ${error instanceof Error ? error.message : 'Unknown error'}`);
  }
}

