import { Command } from 'commander';
import fs from 'fs/promises';
import path from 'path';
import { logger, LogLevel } from '../utils/logger.js';
import { AIRouter, AIRequest } from '../services/ai-router.js';
import { TaskType, Message } from '../interfaces/ai-provider.js';

interface VisionOptions {
  output?: string;
  format?: 'text' | 'json' | 'markdown';
  detail?: 'low' | 'high' | 'auto';
  provider?: string;
  model?: string;
  local?: boolean;
  extract?: 'objects' | 'text' | 'all';
  verbose?: boolean;
}

export default function visionCommand(program: Command) {
  program
    .command('vision')
    .description('AI-powered image analysis using vision-capable providers')
    .argument('<image>', 'Path to image file or URL')
    .argument('[prompt]', 'Analysis prompt (optional, defaults to general description)')
    .option('-o, --output <file>', 'Output analysis to file (JSON format)')
    .option('-f, --format <format>', 'Output format: text, json, or markdown', 'text')
    .option('-d, --detail <level>', 'Analysis detail level: low, high, or auto', 'auto')
    .option('--provider <name>', 'Specific AI provider to use (openai, google, anthropic, ollama, etc.)')
    .option('--model <model>', 'Specific model to use')
    .option('--local', 'Prefer local models', false)
    .option('--extract <type>', 'What to extract: objects, text, or all', 'all')
    .option('-v, --verbose', 'Show detailed output', false)
    .action(async (imagePath: string, prompt: string = '', options: VisionOptions) => {
      // Set log level
      if (options.verbose) {
        logger.setLevel(LogLevel.DEBUG);
      }

      logger.task('Vision Analysis', 'start', `Analyzing image: ${imagePath}`);

      try {
        // Validate and load image
        const imageData = await loadImage(imagePath);
        logger.debug(`Image loaded: ${imageData.length} bytes`);

        // Initialize AI Router
        const router = await initializeAIRouter();

        // Build analysis prompt
        const analysisPrompt = buildVisionPrompt(prompt, options);
        
        // Prepare messages
        const messages: Message[] = [
          { 
            role: 'system', 
            content: 'You are an expert computer vision analyst. Provide detailed, accurate analysis of images.' 
          },
          { 
            role: 'user', 
            content: analysisPrompt 
          }
        ];

        // Prepare AI request
        const aiRequest: AIRequest = {
          messages,
          taskType: TaskType.VISION_ANALYSIS,
          hasImage: true,
          imageData,
          preferLocal: options.local,
          preferredProvider: options.provider,
          options: {
            temperature: 0.1, // Low temperature for factual analysis
            maxTokens: 2000,
            responseFormat: options.format as 'text' | 'json'
          }
        };

        logger.task('AI Processing', 'progress', 'Routing to vision-capable provider...');

        // Analyze image using AI Router
        const response = await router.route(aiRequest);

        logger.task('AI Processing', 'complete', `Used ${response.provider} (${response.model || 'default'})`);

        // Process and format the response
        const analysis = await processVisionResponse(response.content, options);

        // Display usage information
        if (response.usage) {
          logger.debug(`Tokens: ${response.usage.totalTokens} (${response.usage.promptTokens} + ${response.usage.completionTokens})`);
          if (response.usage.cost) {
            logger.debug(`Estimated cost: $${response.usage.cost.toFixed(4)}`);
          }
        }

        // Handle output
        if (options.output) {
          await handleAnalysisOutput(options.output, analysis, options.format);
        } else {
          // Print to console
          displayAnalysis(analysis, options.format ?? 'markdown', imagePath);
        }

        logger.task('Vision Analysis', 'complete', 
          options.output 
            ? `Analysis saved to ${options.output}` 
            : 'Image analysis completed'
        );

      } catch (error) {
        logger.task('Vision Analysis', 'error', error instanceof Error ? error.message : 'Unknown error');
        logger.error('Vision analysis failed:', error);
        process.exit(1);
      }
    });
}

/**
 * Load image from file path or URL
 */
async function loadImage(imagePath: string): Promise<Buffer> {
  try {
    // Check if it's a URL
    if (imagePath.startsWith('http://') || imagePath.startsWith('https://')) {
      const response = await fetch(imagePath);
      if (!response.ok) {
        throw new Error(`Failed to fetch image: ${response.status} ${response.statusText}`);
      }
      const arrayBuffer = await response.arrayBuffer();
      return Buffer.from(arrayBuffer);
    }

    // Check if file exists
    const fullPath = path.resolve(imagePath);
    await fs.access(fullPath);

    // Check file extension
    const ext = path.extname(fullPath).toLowerCase();
    const supportedFormats = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp'];
    
    if (!supportedFormats.includes(ext)) {
      logger.warn(`File extension ${ext} may not be supported. Supported: ${supportedFormats.join(', ')}`);
    }

    // Load file
    return await fs.readFile(fullPath);

  } catch (error) {
    if (error instanceof Error) {
      if (error.message.includes('ENOENT')) {
        throw new Error(`Image file not found: ${imagePath}`);
      }
      throw error;
    }
    throw new Error(`Failed to load image: ${imagePath}`);
  }
}

/**
 * Build vision analysis prompt based on options
 */
function buildVisionPrompt(userPrompt: string, options: VisionOptions): string {
  const parts = [];

  if (userPrompt.trim()) {
    parts.push(userPrompt);
  } else {
    parts.push('Please analyze this image in detail.');
  }

  // Add specific extraction instructions
  switch (options.extract) {
    case 'objects':
      parts.push('\nFocus specifically on identifying and describing all objects, people, animals, or items visible in the image. Include their positions, colors, and relationships.');
      break;
    case 'text':
      parts.push('\nFocus specifically on extracting and transcribing any text, signs, labels, or written content visible in the image.');
      break;
    case 'all':
      parts.push('\nProvide a comprehensive analysis including:');
      parts.push('- Overall scene description');
      parts.push('- All visible objects, people, and their attributes');
      parts.push('- Any text or written content');
      parts.push('- Colors, lighting, and composition');
      parts.push('- Context and setting information');
      break;
  }

  // Add format-specific instructions
  if (options.format === 'json') {
    parts.push('\nStructure your response as valid JSON with appropriate fields for the analysis.');
  } else if (options.format === 'markdown') {
    parts.push('\nFormat your response as clean Markdown with appropriate headers and structure.');
  }

  return parts.join('\n');
}

/**
 * Process vision response based on format and options
 */
async function processVisionResponse(
  response: string, 
  options: VisionOptions
): Promise<any> {
  if (options.format === 'json') {
    try {
      return JSON.parse(response);
    } catch {
      // If JSON parsing fails, wrap response in a simple structure
      return {
        analysis: response,
        format: 'text',
        error: 'Response was not valid JSON, wrapped as text'
      };
    }
  }

  return response;
}

/**
 * Display analysis in console
 */
function displayAnalysis(analysis: any, format: string, imagePath: string): void {
  console.log('\n' + '‚ïê'.repeat(80));
  console.log(`Vision Analysis: ${path.basename(imagePath)}`);
  console.log('‚ïê'.repeat(80));

  if (format === 'json') {
    console.log(JSON.stringify(analysis, null, 2));
  } else if (format === 'markdown') {
    console.log(analysis);
  } else {
    // Text format
    console.log(analysis);
  }

  console.log('‚ïê'.repeat(80) + '\n');
}

/**
 * Handle file output for analysis
 */
async function handleAnalysisOutput(
  outputPath: string, 
  analysis: any, 
  format?: string
): Promise<void> {
  try {
    let content: string;
    let finalPath = outputPath;

    // Determine content and file extension
    if (format === 'json') {
      content = JSON.stringify(analysis, null, 2);
      if (!path.extname(finalPath)) {
        finalPath += '.json';
      }
    } else if (format === 'markdown') {
      content = typeof analysis === 'string' ? analysis : JSON.stringify(analysis, null, 2);
      if (!path.extname(finalPath)) {
        finalPath += '.md';
      }
    } else {
      content = typeof analysis === 'string' ? analysis : JSON.stringify(analysis, null, 2);
      if (!path.extname(finalPath)) {
        finalPath += '.txt';
      }
    }

    // Create directory if needed
    const dir = path.dirname(finalPath);
    await fs.mkdir(dir, { recursive: true });

    // Write file
    await fs.writeFile(finalPath, content, 'utf8');
    logger.success(`Analysis saved to ${finalPath}`);

  } catch (error) {
    throw new Error(`Failed to write analysis file: ${error instanceof Error ? error.message : 'Unknown error'}`);
  }
}

