import { Command } from 'commander';
import fs from 'fs/promises';
import path from 'path';
import { globby } from 'globby';
import { logger, LogLevel } from '../utils/logger.js';
import { AIRouter, AIRequest } from '../services/ai-router.js';
import { TaskType, Message } from '../interfaces/ai-provider.js';

interface TestOptions {
  output?: string;
  type?: 'unit' | 'integration' | 'e2e' | 'all';
  framework?: string;
  language?: string;
  provider?: string;
  model?: string;
  local?: boolean;
  coverage?: boolean;
  mocks?: boolean;
  fixtures?: boolean;
  overwrite?: boolean;
  run?: boolean;
  watch?: boolean;
  verbose?: boolean;
}

interface TestGenerationResult {
  testFiles: Array<{
    originalFile: string;
    testFile: string;
    content: string;
    framework: string;
    testCount: number;
  }>;
  summary: {
    totalFiles: number;
    totalTests: number;
    framework: string;
    coverage: string[];
  };
  setup?: {
    dependencies: string[];
    configFiles: Array<{
      file: string;
      content: string;
    }>;
  };
}

export default function testCommand(program: Command) {
  program
    .command('test')
    .description('AI-powered test generation and execution')
    .argument('[files...]', 'Files or patterns to generate tests for (defaults to source files)')
    .option('-o, --output <dir>', 'Output directory for test files (defaults to tests/ or __tests__/)')
    .option('-t, --type <type>', 'Test type: unit, integration, e2e, or all', 'unit')
    .option('-f, --framework <framework>', 'Testing framework (jest, vitest, mocha, pytest, etc.)')
    .option('-l, --language <lang>', 'Programming language (auto-detected if not specified)')
    .option('--provider <name>', 'Specific AI provider to use')
    .option('--model <model>', 'Specific model to use')
    .option('--local', 'Prefer local models', false)
    .option('--coverage', 'Generate tests for maximum code coverage', false)
    .option('--mocks', 'Include mock examples', true)
    .option('--fixtures', 'Generate test fixtures and data', false)
    .option('--overwrite', 'Overwrite existing test files', false)
    .option('-r, --run', 'Run tests after generation', false)
    .option('-w, --watch', 'Watch mode for continuous testing', false)
    .option('-v, --verbose', 'Show detailed output', false)
    .action(async (files: string[], options: TestOptions) => {
      // Set log level
      if (options.verbose) {
        logger.setLevel(LogLevel.DEBUG);
      }

      logger.task('Test Generation', 'start', 'Starting AI-powered test generation');

      try {
        // Resolve files to generate tests for
        const sourceFiles = await resolveSourceFiles(files, options);
        
        if (sourceFiles.length === 0) {
          logger.warn('No source files found to generate tests for');
          return;
        }

        logger.debug(`Found ${sourceFiles.length} source files:`, sourceFiles.map(f => path.relative(process.cwd(), f)));

        // Detect or validate testing framework
        const framework = await detectTestingFramework(options);
        logger.info(`Using testing framework: ${framework}`);

        // Initialize AI Router
        const router = await initializeAIRouter();

        // Generate tests
        const result: TestGenerationResult = {
          testFiles: [],
          summary: {
            totalFiles: sourceFiles.length,
            totalTests: 0,
            framework,
            coverage: []
          }
        };

        for (let i = 0; i < sourceFiles.length; i++) {
          const sourceFile = sourceFiles[i];
          if (!sourceFile) continue;

          logger.task('Test Generation', 'progress', 
            `Generating tests for ${path.relative(process.cwd(), sourceFile)} (${i + 1}/${sourceFiles.length})`
          );

          try {
            const testResult = await generateTestsForFile(sourceFile, framework, router, options);
            result.testFiles.push(testResult);
            result.summary.totalTests += testResult.testCount;

          } catch (error) {
            logger.error(`Failed to generate tests for ${sourceFile}:`, error);
            continue;
          }
        }

        // Generate setup and configuration if needed
        if (result.testFiles.length > 0) {
          result.setup = await generateTestSetup(framework, options);
        }

        logger.task('Test Generation', 'complete', 
          `Generated ${result.summary.totalTests} tests in ${result.testFiles.length} files`
        );

        // Write test files
        await writeTestFiles(result, options);

        // Run tests if requested
        if (options.run) {
          await runGeneratedTests(framework, options);
        }

        // Display summary
        displayTestSummary(result);

      } catch (error) {
        logger.task('Test Generation', 'error', error instanceof Error ? error.message : 'Unknown error');
        logger.error('Test generation failed:', error);
        process.exit(1);
      }
    });
}

/**
 * Resolve source files to generate tests for
 */
async function resolveSourceFiles(files: string[], options: TestOptions): Promise<string[]> {
  let sourceFiles: string[] = [];

  if (files.length > 0) {
    // Use provided files/patterns
    sourceFiles = await globby(files, { 
      gitignore: true,
      onlyFiles: true,
      absolute: true
    });
  } else {
    // Default patterns for common source files
    const language = options.language || await detectProjectLanguage();
    const patterns = getSourceFilePatterns(language);
    
    sourceFiles = await globby(patterns, {
      gitignore: true,
      onlyFiles: true,
      absolute: true,
      cwd: process.cwd()
    });
  }

  // Filter out existing test files
  sourceFiles = sourceFiles.filter(file => !isTestFile(file));

  // Filter by language if specified
  if (options.language) {
    sourceFiles = sourceFiles.filter(file => {
      const ext = path.extname(file).toLowerCase();
      return getLanguageForExtension(ext) === options.language!.toLowerCase();
    });
  }

  return sourceFiles;
}

/**
 * Detect testing framework based on project
 */
async function detectTestingFramework(options: TestOptions): Promise<string> {
  if (options.framework) {
    return options.framework;
  }

  try {
    const packageJsonPath = path.join(process.cwd(), 'package.json');
    
    if (await fs.access(packageJsonPath).then(() => true).catch(() => false)) {
      const packageJson = JSON.parse(await fs.readFile(packageJsonPath, 'utf8'));
      const dependencies = { ...packageJson.dependencies, ...packageJson.devDependencies };

      // Check for common testing frameworks
      if (dependencies.vitest) return 'vitest';
      if (dependencies.jest) return 'jest';
      if (dependencies.mocha) return 'mocha';
      if (dependencies['@testing-library/react']) return 'jest'; // React Testing Library typically uses Jest
      if (dependencies.cypress) return 'cypress';
      if (dependencies.playwright) return 'playwright';
    }

    // Check for Python
    if (await fs.access(path.join(process.cwd(), 'requirements.txt')).then(() => true).catch(() => false) ||
        await fs.access(path.join(process.cwd(), 'pyproject.toml')).then(() => true).catch(() => false)) {
      return 'pytest';
    }

    // Check for Go
    if (await fs.access(path.join(process.cwd(), 'go.mod')).then(() => true).catch(() => false)) {
      return 'go-test';
    }

    // Check for Rust
    if (await fs.access(path.join(process.cwd(), 'Cargo.toml')).then(() => true).catch(() => false)) {
      return 'rust-test';
    }

    // Check for Java
    if (await fs.access(path.join(process.cwd(), 'pom.xml')).then(() => true).catch(() => false)) {
      return 'junit';
    }

  } catch (error) {
    logger.debug('Error detecting testing framework:', error);
  }

  // Default to Jest for JavaScript/TypeScript projects
  return 'jest';
}

/**
 * Generate tests for a single file
 */
async function generateTestsForFile(
  sourceFile: string,
  framework: string,
  router: AIRouter,
  options: TestOptions
): Promise<{
  originalFile: string;
  testFile: string;
  content: string;
  framework: string;
  testCount: number;
}> {
  
  const sourceContent = await fs.readFile(sourceFile, 'utf8');
  const language = options.language || getLanguageForExtension(path.extname(sourceFile));
  const relativePath = path.relative(process.cwd(), sourceFile);

  // Build test generation prompt
  const systemPrompt = buildTestSystemPrompt(language, framework, options);
  const userPrompt = buildTestUserPrompt(relativePath, sourceContent, language, framework, options);

  const messages: Message[] = [
    { role: 'system', content: systemPrompt },
    { role: 'user', content: userPrompt }
  ];

  const aiRequest: AIRequest = {
    messages,
    taskType: TaskType.CODE_GENERATION,
    preferLocal: options.local,
    preferredProvider: options.provider,
    context: {
      language,
      framework,
      projectType: detectProjectType(process.cwd())
    },
    options: {
      temperature: 0.3, // Balanced for test creativity and consistency
      maxTokens: 4000,
      responseFormat: 'text'
    }
  };

  const response = await router.route(aiRequest);
  
  // Extract test code
  const testContent = extractTestCode(response.content, language);
  const testCount = countTestCases(testContent, framework);

  // Determine test file path
  const testFile = generateTestFilePath(sourceFile, framework, options.output);

  return {
    originalFile: sourceFile,
    testFile,
    content: testContent,
    framework,
    testCount
  };
}

/**
 * Build system prompt for test generation
 */
function buildTestSystemPrompt(language: string, framework: string, options: TestOptions): string {
  const parts = [
    `You are an expert ${language.toUpperCase()} developer and test engineer specializing in ${framework}.`,
    '',
    `Generate comprehensive ${options.type} tests that:`,
    '- Cover all public methods and functions',
    '- Test both happy path and edge cases',
    '- Include proper error handling tests',
    '- Follow testing best practices',
    '- Use appropriate assertions and matchers',
    ''
  ];

  // Framework-specific instructions
  switch (framework) {
    case 'jest':
      parts.push(
        'Jest-specific requirements:',
        '- Use describe() blocks for organization',
        '- Use it() or test() for individual test cases',
        '- Use appropriate Jest matchers (toEqual, toBe, toThrow, etc.)',
        '- Include beforeEach/afterEach for setup/cleanup when needed'
      );
      break;
    case 'vitest':
      parts.push(
        'Vitest-specific requirements:',
        '- Use describe() and it() functions',
        '- Import from "vitest" for utilities',
        '- Use vi.mock() for mocking when needed',
        '- Include proper TypeScript types'
      );
      break;
    case 'pytest':
      parts.push(
        'Pytest-specific requirements:',
        '- Use pytest fixtures for setup',
        '- Follow naming convention (test_* functions)',
        '- Use assert statements with clear messages',
        '- Include parametrized tests when appropriate'
      );
      break;
    case 'go-test':
      parts.push(
        'Go testing requirements:',
        '- Use *testing.T parameter',
        '- Follow TestXxx naming convention',
        '- Use t.Error, t.Fatal, t.Run appropriately',
        '- Include table-driven tests when suitable'
      );
      break;
  }

  if (options.mocks) {
    parts.push('- Include mock examples where external dependencies exist');
  }

  if (options.fixtures) {
    parts.push('- Generate test fixtures and sample data');
  }

  if (options.coverage) {
    parts.push('- Ensure comprehensive test coverage of all code paths');
  }

  parts.push(
    '',
    'Provide clean, well-commented test code that can be run immediately.',
    'Include necessary imports and setup code.'
  );

  return parts.join('\n');
}

/**
 * Build user prompt for specific file
 */
function buildTestUserPrompt(
  filePath: string,
  content: string,
  language: string,
  framework: string,
  options: TestOptions
): string {
  return [
    `Generate ${options.type} tests for this ${language} file using ${framework}:`,
    '',
    `File: ${filePath}`,
    '',
    '```' + language,
    content,
    '```',
    '',
    'Focus on testing all exported functions, classes, and methods.',
    'Include appropriate test descriptions and organize tests logically.'
  ].join('\n');
}

/**
 * Extract test code from AI response
 */
function extractTestCode(response: string, language: string): string {
  // Try to extract code from markdown code blocks
  const languageAliases = getLanguageAliases(language);
  
  for (const alias of languageAliases) {
    const codeBlockRegex = new RegExp(`\`\`\`${alias}\\n([\\s\\S]*?)\`\`\``, 'gi');
    const match = codeBlockRegex.exec(response);
    if (match && match[1]) {
      return match[1].trim();
    }
  }
  
  // Try general code blocks
  const generalCodeBlockRegex = /```[\w]*\n([\s\S]*?)```/gi;
  const match = generalCodeBlockRegex.exec(response);
  if (match && match[1]) {
    return match[1].trim();
  }
  
  // If no code blocks found, return the whole response cleaned up
  return response.trim();
}

/**
 * Count test cases in generated code
 */
function countTestCases(testContent: string, framework: string): number {
  let count = 0;
  
  switch (framework) {
    case 'jest':
    case 'vitest':
      count += (testContent.match(/\b(it|test)\s*\(/g) || []).length;
      break;
    case 'pytest':
      count += (testContent.match(/def test_\w+/g) || []).length;
      break;
    case 'go-test':
      count += (testContent.match(/func Test\w+/g) || []).length;
      break;
    case 'rust-test':
      count += (testContent.match(/#\[test\]/g) || []).length;
      break;
    case 'junit':
      count += (testContent.match(/@Test/g) || []).length;
      break;
    default:
      // Fallback: count function/method definitions
      count = (testContent.match(/function\s+\w+|def\s+\w+|func\s+\w+/g) || []).length;
  }
  
  return count;
}

/**
 * Generate test file path
 */
function generateTestFilePath(sourceFile: string, framework: string, outputDir?: string): string {
  const sourceDir = path.dirname(sourceFile);
  const baseName = path.basename(sourceFile, path.extname(sourceFile));
  const ext = path.extname(sourceFile);
  
  let testDir: string;
  let testFileName: string;

  if (outputDir) {
    testDir = path.resolve(outputDir);
  } else {
    // Default test directory based on framework/language
    if (framework === 'jest' || framework === 'vitest') {
      testDir = path.join(sourceDir, '__tests__');
    } else {
      testDir = path.join(process.cwd(), 'tests');
    }
  }

  // Generate test file name based on convention
  switch (framework) {
    case 'jest':
    case 'vitest':
      testFileName = `${baseName}.test${ext}`;
      break;
    case 'pytest':
      testFileName = `test_${baseName}.py`;
      break;
    case 'go-test':
      testFileName = `${baseName}_test.go`;
      break;
    case 'rust-test':
      testFileName = `${baseName}_test.rs`;
      break;
    default:
      testFileName = `${baseName}.test${ext}`;
  }

  return path.join(testDir, testFileName);
}

/**
 * Generate test setup and configuration
 */
async function generateTestSetup(framework: string, options: TestOptions): Promise<{
  dependencies: string[];
  configFiles: Array<{ file: string; content: string; }>;
}> {
  const setup = {
    dependencies: [] as string[],
    configFiles: [] as Array<{ file: string; content: string; }>
  };

  switch (framework) {
    case 'jest':
      setup.dependencies = ['jest', '@types/jest'];
      setup.configFiles.push({
        file: 'jest.config.js',
        content: generateJestConfig(options)
      });
      break;
    case 'vitest':
      setup.dependencies = ['vitest', '@vitest/ui'];
      setup.configFiles.push({
        file: 'vitest.config.ts',
        content: generateVitestConfig(options)
      });
      break;
    case 'pytest':
      setup.dependencies = ['pytest', 'pytest-cov'];
      setup.configFiles.push({
        file: 'pytest.ini',
        content: generatePytestConfig(options)
      });
      break;
  }

  return setup;
}

/**
 * Write all test files to disk
 */
async function writeTestFiles(result: TestGenerationResult, options: TestOptions): Promise<void> {
  for (const testFile of result.testFiles) {
    const testDir = path.dirname(testFile.testFile);
    
    // Create directory if it doesn't exist
    await fs.mkdir(testDir, { recursive: true });

    // Check if file exists
    const fileExists = await fs.access(testFile.testFile).then(() => true).catch(() => false);
    
    if (fileExists && !options.overwrite) {
      logger.warn(`Test file ${testFile.testFile} already exists. Use --overwrite to replace it.`);
      continue;
    }

    // Write test file
    await fs.writeFile(testFile.testFile, testFile.content, 'utf8');
    logger.success(`Generated test: ${path.relative(process.cwd(), testFile.testFile)}`);
  }

  // Write setup files if any
  if (result.setup) {
    for (const configFile of result.setup.configFiles) {
      const configPath = path.join(process.cwd(), configFile.file);
      const configExists = await fs.access(configPath).then(() => true).catch(() => false);
      
      if (!configExists) {
        await fs.writeFile(configPath, configFile.content, 'utf8');
        logger.info(`Created config: ${configFile.file}`);
      }
    }
  }
}

/**
 * Run generated tests
 */
async function runGeneratedTests(framework: string, options: TestOptions): Promise<void> {
  logger.task('Test Execution', 'start', `Running tests with ${framework}`);
  
  try {
    const { spawn } = require('child_process');
    let command: string;
    let args: string[] = [];

    switch (framework) {
      case 'jest':
        command = 'npx';
        args = ['jest'];
        if (options.watch) args.push('--watch');
        if (options.coverage) args.push('--coverage');
        break;
      case 'vitest':
        command = 'npx';
        args = ['vitest'];
        if (options.watch) args.push('--watch');
        if (options.coverage) args.push('--coverage');
        break;
      case 'pytest':
        command = 'python';
        args = ['-m', 'pytest'];
        if (options.coverage) args.push('--cov');
        break;
      case 'go-test':
        command = 'go';
        args = ['test', './...'];
        if (options.coverage) args.push('-cover');
        break;
      default:
        throw new Error(`Test execution not implemented for ${framework}`);
    }

    const child = spawn(command, args, { 
      stdio: 'inherit',
      cwd: process.cwd()
    });

    child.on('close', (code: number | null) => {
      if (code === 0) {
        logger.task('Test Execution', 'complete', 'All tests passed');
      } else {
        logger.task('Test Execution', 'error', `Tests failed with exit code ${code}`);
      }
    });

  } catch (error) {
    logger.task('Test Execution', 'error', error instanceof Error ? error.message : 'Unknown error');
  }
}

