// Utility functions

function detectProjectLanguage(): Promise<string> {
  // Implementation similar to other detect functions
  return Promise.resolve('javascript');
}

function getSourceFilePatterns(language: string): string[] {
  const patterns: Record<string, string[]> = {
    'javascript': ['**/*.js', '**/*.jsx', '!**/*.test.js', '!**/*.spec.js'],
    'typescript': ['**/*.ts', '**/*.tsx', '!**/*.test.ts', '!**/*.spec.ts', '!**/*.d.ts'],
    'python': ['**/*.py', '!**/test_*.py', '!**/*_test.py'],
    'go': ['**/*.go', '!**/*_test.go'],
    'rust': ['**/*.rs', '!**/tests/**'],
    'java': ['**/*.java', '!**/test/**'],
  };
  
  return patterns[language] || patterns['javascript']!;
}

function isTestFile(filePath: string): boolean {
  const fileName = path.basename(filePath);
  const testPatterns = [
    /\.test\./,
    /\.spec\./,
    /^test_/,
    /_test\./,
    /Test\./,
    /tests?[\/\\]/
  ];
  
  return testPatterns.some(pattern => pattern.test(fileName) || pattern.test(filePath));
}

function getLanguageForExtension(ext: string): string {
  const languageMap: Record<string, string> = {
    '.js': 'javascript',
    '.jsx': 'javascript',
    '.ts': 'typescript',
    '.tsx': 'typescript',
    '.py': 'python',
    '.java': 'java',
    '.go': 'go',
    '.rs': 'rust',
    '.php': 'php',
    '.rb': 'ruby',
    '.c': 'c',
    '.cpp': 'cpp',
    '.cs': 'csharp'
  };

  return languageMap[ext.toLowerCase()] || 'javascript';
}

function getLanguageAliases(language: string): string[] {
  const aliases: Record<string, string[]> = {
    'javascript': ['javascript', 'js'],
    'typescript': ['typescript', 'ts'],
    'python': ['python', 'py'],
    'java': ['java'],
    'go': ['go', 'golang'],
    'rust': ['rust', 'rs']
  };
  
  return aliases[language] || [language];
}

function detectProjectType(cwd: string): string {
  try {
    if (require('fs').existsSync(path.join(cwd, 'package.json'))) return 'nodejs';
    if (require('fs').existsSync(path.join(cwd, 'requirements.txt'))) return 'python';
    if (require('fs').existsSync(path.join(cwd, 'Cargo.toml'))) return 'rust';
    if (require('fs').existsSync(path.join(cwd, 'go.mod'))) return 'go';
    if (require('fs').existsSync(path.join(cwd, 'pom.xml'))) return 'java';
    return 'general';
  } catch {
    return 'general';
  }
}

// Configuration generators

function generateJestConfig(options: TestOptions): string {
  return `module.exports = {
  testEnvironment: 'node',
  testMatch: [
    '**/__tests__/**/*.(test|spec).(js|jsx|ts|tsx)',
    '**/*.(test|spec).(js|jsx|ts|tsx)'
  ],
  collectCoverageFrom: [
    'src/**/*.(js|jsx|ts|tsx)',
    '!src/**/*.d.ts',
    '!src/**/*.test.*',
    '!src/**/*.spec.*'
  ],
  ${options.coverage ? 'collectCoverage: true,' : ''}
  coverageDirectory: 'coverage',
  coverageReporters: ['text', 'lcov', 'html']
};`;
}

function generateVitestConfig(options: TestOptions): string {
  return `import { defineConfig } from 'vitest/config'

export default defineConfig({
  test: {
    globals: true,
    environment: 'node',
    ${options.coverage ? "coverage: { enabled: true, reporter: ['text', 'json', 'html'] }," : ''}
    include: ['**/*.{test,spec}.{js,mjs,cjs,ts,mts,cts,jsx,tsx}'],
    exclude: ['node_modules', 'dist', '.idea', '.git', '.cache']
  }
})`;
}

function generatePytestConfig(options: TestOptions): string {
  return `[tool:pytest]
testpaths = tests
python_files = test_*.py
python_classes = Test*
python_functions = test_*
${options.coverage ? 'addopts = --cov=src --cov-report=html --cov-report=term' : ''}
`;
}

